body,
html {
  max-height: 100px !important;
}

* {
  font-family: "Roboto Slab", serif;
}

:root {
  --effect: hover 1s linear infinite;
}


/* Slideshow */

.default-nav {
  display: none !important;
}

a {
  color: white;
}


.add_green i {
  transition: 2s ease;
  color: #00c853 !important;
}

.add_red i {
  transition: 2s ease;
  color: red !important;
}

.add_yellow i {
  transition: 2s ease;
  color: yellow !important;
}

img,
image {
  image-rendering: -moz-crisp-edges;
  /* Firefox */
  image-rendering: -o-crisp-edges;
  /* Opera */
  image-rendering: -webkit-optimize-contrast;
  /* Webkit (non-standard naming) */
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
  /* IE (non-standard property) */
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes load {
  0% {
    opacity: 0.08;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    letter-spacing: 3px;
  }
}

@keyframes load {
  0% {
    opacity: 0.08;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    letter-spacing: 3px;
  }
}

.transition-footer-top {
  bottom: 87.5% !important;
  -webkit-transition: 1.75s;
  margin-bottom: 0px !important;
}

.transition-footer-bottom {
  bottom: 0% !important;
  -webkit-transition: 1.25s;
  margin-bottom: 0px !important;
}

.transition-container-top {
  top: 9% !important;
  -webkit-transition: 1.75s;
  margin-bottom: 0px !important;
}

.transition-container-bottom {
  top: 100% !important;
  -webkit-transition: 1.25s;
  height: 0 !important;
  margin-bottom: 0px !important;
}

.transition-container-hide {
  display: none !important;
}

